.blogs{
    display: flex;   
    flex-direction: row;
    flex-wrap: wrap;
    margin: 20px;
    padding: 10px;
    justify-content: space-between;
    background-color: #f2f6ff;
    padding: 2% 14%;
    font-size: 16px;
    margin: 0;
    box-sizing: border-box;  
      
  }
.blog{
    border-radius: 5px;
    border: 1px solid rgba(0,168,255,.3);
    box-shadow: 0 3px 10px 0 rgb(0 0 0 / 10%);
    width:250px;      
    height: auto;
    margin: 0 auto;
    margin-bottom: 20px;
    background-color: #fff;
    
  }
  .blog-text{
      padding:12px 20px;
      text-align: start;    
      
  }
  .blog > img{
    width: 100%;    
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
.blog-title{
    display:-webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 22px;
    margin-top: 0;
    margin-bottom:10px ;
}
.blog-author{
    color: #0096ff;
    font-size: 16px;
    margin: 0;
}
.blog-author > span{
    color: #000;
    font-size: 14px;
    font-weight: 400;
}
.blog-discription{
    font-family: Raleway;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.43;
    letter-spacing: .35px;
    text-align: left;
    color: #000;
    margin: .3em 0;
    white-space: pre-wrap;
    opacity: .5;
    padding-top: 5px;
    display:-webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;

}