#topbar{   
    margin-top: 0px; 
    display: flex;
    align-items: center;
    height:80px;
    width:100%;
    padding: 1em 4.875em;
    background-color: #fff;
    box-shadow:  0 1px 1px #ccc;
    justify-content: space-between;
    box-sizing: border-box; 
    position: fixed; 
    top: 0;
    left:0;
    z-index: 9;
      
}
#topbar > div{
    display: flex;
    justify-content: space-between;
}
.logo{
    cursor: pointer;
    text-align: left;
}
.eText{
    color: #0084ff;     
    line-height: 1.08;
    letter-spacing: -.9px;
    font-weight: 900;
    font-size: 2.1em;
    margin-top: 0;
    width: 130px;
}
.stories{
    font-size: 1em;
    font-weight: 500;
    font-style: italic;
    font-stretch: normal;
    line-height: .8;
    letter-spacing: .9px;
    color: #000;
    margin: 0;
}
.t2{
    display: flex;   
    align-items: center;
    margin: 0 20px 0 2.5em;
    justify-content: space-between;
    padding: .25em 0;
    box-sizing: border-box;
}
.dropdown{
    display: flex;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: .9px;
    text-align: center;
    cursor: pointer;
    font-size: 14px;
    color: #000;
    width: auto;
    justify-content: space-around;
    margin: 0 10px;
    padding: 12px 10px;
    border-bottom: 4px solid transparent;
    box-sizing: border-box;
}
.dropdown div{
    margin-right: 7px;
}
.dropdown img{
    margin-top: 10px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: .9px;
    text-align: center;
    cursor: pointer;    
    padding-left: 0;
    font-size: 14px;
    width: 15px;
    height: 8px;
    color: #000;
}
.diffPara{
    min-width: 20em;
    opacity: .6;
    font-family: Raleway,sans-serif;
    font-size: 15px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1;
    letter-spacing: .4px;
    text-align: left;
    color: rgba(0,0,0,.6);
    margin-top: .5em;
}
.top-btn{
    width: auto;
    font-size: 0.75em;
    padding: 0.5em 2em;
    margin: 0px 1em 0px 0px;
    box-shadow: none;
    border: none;
    cursor: pointer;
    box-sizing: border-box;
    color: #fff;
    outline: none;
    font-family: Raleway,sans-serif;
    background: linear-gradient(
        269deg
        ,#0048ff,#0096ff);
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: .03em;
    min-width: 136px;
    height: 42px;
    border-radius: 25px;
}
button{
    display: inline-block;
    text-align: center;
    align-items: flex-start;
    font: 400 13.3333px Arial;
}
@media only screen and (max-width: 800px) {  
    #topbar{
        padding: 0.5em 0.5em;
        margin-right: 0;
    }
    .diffpara-wrapper{
        display: none;
    }
    .top-btn{
        padding:0.5em 1em;
    }
    
}
@media only screen and (max-width: 500px) { 
    #topbar{
        padding: .6em 1em;
    } 
    .diffPara{
        display: none;
    }
    .dropdown{
        display: none;
    }
    
}