.latest-post{
    background-color: #f2f6ff;
    padding: 2% 14%;
    font-size: 16px;
    
}
.post-head{
    font-size: 26px;
    font-weight: 500;
    text-align: start;
    margin-bottom: 0;
   
}
.capsules{    
    display: flex;  
    flex-wrap: wrap; 
    font-size: 14px;
    font-weight: bold; 
    
}  
.capsule{    
    padding: 5px 16px;
    margin: 5px;
    color: #0084ff; 
    font-size: 14px;
    font-weight: 400;
    border: 1px solid #0084ff;
    border-radius: 15px;
    align-items: center;
    width: auto; 
    cursor: pointer;
   
    
}
.filterText{
    display: flex;    
    align-items: center;
}
.capsule-active{
    background-color: #0084ff;
    border-radius: 15px;
    align-items: center;
    color: #fff;
    border: 1px solid #0084ff;
    padding: 5px 16px;
    margin: 5px;
    
   
}